*, :after, :before {
  box-sizing: inherit;
}

:root {
  --backgroundColor: #222;
  --color: white;
  box-sizing: border-box;
}

@media (prefers-color-scheme: light) {
  :root {
    --backgroundColor: white;
    --color: #222;
  }
}

body {
  background-color: var(--backgroundColor);
  color: var(--color);
  font-family: sans-serif;
}

.section {
  padding-top: 30px;
}

.form-label {
  width: 100%;
}

.form-control {
  color: var(--backgroundColor);
  font-family: inherit;
}

.form {
  max-width: 400px;
  flex-direction: column;
  gap: 1em;
  padding: 2.5em;
  display: flex;
}

.form-output {
  font-size: 1.2em;
}

.form-output div {
  border: 1px solid var(--color);
  padding: 10px;
}

.form-output div:not(:last-child) {
  border-bottom: none;
}

